:root {
  --font-montserrat: "Montserrat", sans-serif;
  --color-white: #fff;
  --color-siniy: #355ddb;
  --color-belyy: #fcfcfc;
  --color-chernyy: #0e0e0e;
  --color-black: #101010;
  --color-seryy-tekst: #7d7d7d;
  --color-sernye-linii: #c9c9c9;
  --color-svetlo-seryy-linii: #e1e1e1;
  --color-header-inner-list-bg: #f4f4f4;
  --scrollbar-width: 2px;
  --scrollbar-track-color: var(--color-svetlo-seryy-linii, #e1e1e1);
  --scrollbar-thumb-color: var(--color-siniy, #355ddb);
  --mobile-bottom-height: 168px;
  --mobile-header-height: 65px;
}

@font-face {
  font-display: swap;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/montserrat-v26-cyrillic-ext_latin-regular.woff2") format("woff2"), url("../fonts/montserrat-v26-cyrillic-ext_latin-regular.ttf") format("truetype");
}
@font-face {
  font-display: swap;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/montserrat-v26-cyrillic-ext_latin-500.woff2") format("woff2"), url("../fonts/montserrat-v26-cyrillic-ext_latin-500.ttf") format("truetype");
}
@font-face {
  font-display: swap;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/montserrat-v26-cyrillic-ext_latin-600.woff2") format("woff2"), url("../fonts/montserrat-v26-cyrillic-ext_latin-600.ttf") format("truetype");
}
/*===== global styles =====*/
*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  scrollbar-width: thin;
  scrollbar-color: var(--scrollbar-thumb-color) var(--scrollbar-track-color);
}
*::-webkit-scrollbar {
  width: var(--scrollbar-width);
  height: var(--scrollbar-width);
}
*::-webkit-scrollbar-track {
  background-color: var(--scrollbar-track-color);
}
*::-webkit-scrollbar-thumb {
  background-color: var(--scrollbar-thumb-color);
}
*::-webkit-resizer, *::-webkit-scrollbar-button, *::-webkit-scrollbar-corner {
  display: none;
}

html {
  min-width: 360px;
  scrollbar-gutter: stable;
  scroll-behavior: smooth;
}

body {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  min-width: 360px;
  display: flex;
  flex-direction: column;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: var(--color-chernyy, #0e0e0e);
  background-color: var(--color-white, #fff);
  background-color: #838383;
  font-family: var(--font-montserrat, "Montserrat", sans-serif);
  font-weight: 400;
}
body.lock {
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 1580px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1300px) {
  .container {
    max-width: 1084px;
    padding: 0 30px;
  }
}
@media (max-width: 1200px) {
  .container {
    max-width: 100%;
    padding: 0 33px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

a {
  color: inherit;
  text-decoration: none;
  outline: none;
}

html,
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul,
li,
dl,
dd {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
}

ol,
ul {
  list-style: none;
}

button,
input {
  padding: 0;
  font-family: inherit;
  font-size: inherit;
  border: none;
  border-radius: 0;
  appearance: none;
  outline: none;
  color: inherit;
  background-color: transparent;
}

button {
  cursor: pointer;
}

textarea {
  border-radius: 0;
}

:active,
:hover,
:focus {
  outline: 0;
  outline-offset: 0;
}

html,
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
  padding: 0;
}

* {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  outline: none;
}

ul,
ol {
  list-style: none;
}

img {
  image-rendering: -webkit-optimize-contrast;
  margin: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

input,
button,
textarea,
select {
  font: inherit;
  line-height: inherit;
}

.header_art6 {
  position: relative;
  z-index: 10;
}
@media (max-width: 767px) {
  .header_art6 {
    position: sticky;
    top: 0;
  }
}

.header_art6__overlay {
  position: fixed;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: opacity 0.2s ease-out, visibility 0.2s ease-out;
}
.extra-menu-open .header_art6__overlay, .mobile-menu-open .header_art6__overlay {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
}

.header_art6__inner {
  position: relative;
  z-index: 5;
  background-color: var(--color-white, #fff);
  font-family: var(--font-montserrat, "Montserrat", sans-serif);
  border-bottom: 1px solid var(--color-white, #fff);
}
.extra-menu-open .header_art6__inner, .mobile-menu-open .header_art6__inner {
  border-bottom-color: var(--color-sernye-linii, #c9c9c9);
}

.header_art6__container {
  display: flex;
  gap: 0 40px;
  justify-content: space-between;
  align-items: center;
  min-height: 100px;
  padding-top: 23px;
  padding-bottom: 23px;
}
@media (max-width: 767px) {
  .header_art6__container {
    min-height: 65px;
    padding-top: 5px;
    padding-bottom: 5px;
  }
}

.header_art6__logo {
  flex: 0 0 auto;
  max-width: 213px;
}
@media (max-width: 1600px) {
  .header_art6__logo {
    max-width: 189px;
  }
}

.header_art6__logo_link {
  display: block;
  width: 100%;
}

.header_art6__logo_img {
  display: block;
  width: 100%;
}
.header_art6__logo_img img {
  display: block;
  width: 100%;
  height: auto;
}

.header_art6__menu,
.header_art6__menu_list {
  display: flex;
  gap: 0 40px;
  justify-content: center;
  align-items: center;
}

@media (max-width: 767px) {
  .header_art6__menu {
    display: none;
  }
}

.header_art6__menu_btn {
  display: flex;
  align-items: center;
  font-weight: 500;
  font-size: 16px;
  line-height: 150%;
  text-transform: uppercase;
  color: var(--color-chernyy, #0e0e0e);
  transition: color 0.2s ease-out;
}
@media (hover: hover) {
  .header_art6__menu_btn:hover {
    color: var(--color-siniy, #355ddb);
  }
  .header_art6__menu_btn:hover .header_art6__burger span, .header_art6__menu_btn:hover .header_art6__burger::before, .header_art6__menu_btn:hover .header_art6__burger::after {
    color: var(--color-siniy, #355ddb);
    background-color: var(--color-siniy, #355ddb);
  }
}

.header_art6__burger {
  position: relative;
  display: block;
  width: 40px;
  height: 40px;
}
.header_art6__burger::before, .header_art6__burger::after {
  content: "";
  transition: transform 0.2s ease-out, background-color 0.2s ease-out;
}
.header_art6__burger span, .header_art6__burger::before, .header_art6__burger::after {
  position: absolute;
  top: 50%;
  left: 50%;
  display: block;
  width: 23px;
  height: 2px;
  background-color: #1d1d1f;
  border-radius: 2px;
}
.header_art6__burger span {
  transform: translate(-50%, -50%);
  transition: opacity 0.2s ease-out, background-color 0.2s ease-out;
}
.header_art6__burger::before {
  transform: translate(-50%, -8px);
}
.header_art6__burger::after {
  transform: translate(-50%, 6px);
}
.active .header_art6__burger span {
  opacity: 0;
}
.active .header_art6__burger::before {
  transform: translate(-50%, 0) rotate(45deg);
}
.active .header_art6__burger::after {
  transform: translate(-50%, 0) rotate(-45deg);
}

@media (max-width: 1200px) {
  .header_art6__menu_list {
    display: none;
  }
}

.header_art6__menu_item > a {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 13px;
  line-height: 185%;
  text-transform: uppercase;
  color: var(--color-black, #101010);
  transition: color 0.2s ease-out;
  text-wrap: nowrap;
}
@media (hover: hover) {
  .header_art6__menu_item:hover > a {
    color: var(--color-siniy, #355ddb);
  }
  .header_art6__menu_item.item_has_dropdown:hover::before {
    opacity: 0;
  }
  .header_art6__menu_item.item_has_dropdown:hover::after {
    opacity: 1;
  }
}
.header_art6__menu_item.item_has_dropdown {
  position: relative;
  padding-right: 17px;
}
.header_art6__menu_item.item_has_dropdown::before, .header_art6__menu_item.item_has_dropdown::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 2px;
  display: block;
  width: 9px;
  height: 6px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  transition: opacity 0.2s ease-out, transform 0.2s ease-out;
  transform: translateY(-50%);
}
.header_art6__menu_item.item_has_dropdown::before {
  background-image: url("data:image/svg+xml,%3Csvg width='9' height='6' viewBox='0 0 9 6' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg clip-path='url(%23clip0_149_966)'%3E%3Cpath d='M0.75 1.125L4.5 4.875L8.25 1.125' stroke='%23101010' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round' /%3E%3C/g%3E%3Cdefs%3E%3CclipPath id='clip0_149_966'%3E%3Crect width='9' height='6' fill='white' /%3E%3C/clipPath%3E%3C/defs%3E%3C/svg%3E");
  opacity: 1;
}
.header_art6__menu_item.item_has_dropdown::after {
  background-image: url("data:image/svg+xml,%3Csvg width='9' height='6' viewBox='0 0 9 6' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg clip-path='url(%23clip0_149_966)'%3E%3Cpath d='M0.75 1.125L4.5 4.875L8.25 1.125' stroke='%23355ddb' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round' /%3E%3C/g%3E%3Cdefs%3E%3CclipPath id='clip0_149_966'%3E%3Crect width='9' height='6' fill='white' /%3E%3C/clipPath%3E%3C/defs%3E%3C/svg%3E");
  opacity: 0;
}
.header_art6__menu_item.item_has_dropdown.active::before, .header_art6__menu_item.item_has_dropdown.active::after {
  transform: translateY(-50%) scale(-1);
}
@media (max-width: 1300px) {
  .header_art6__menu_item:nth-child(n+3) {
    display: none;
  }
}

.header_art6__panel {
  flex: 0 0 auto;
  display: flex;
  gap: 0 25px;
  justify-content: center;
  align-items: center;
}
@media (max-width: 767px) {
  .header_art6__panel {
    gap: 0 7px;
  }
}

.header_art6__panel_phone {
  font-weight: 500;
  font-size: 16px;
  line-height: 150%;
  text-transform: uppercase;
  color: var(--color-chernyy, #0e0e0e);
  transition: color 0.2s ease-out;
  text-wrap: nowrap;
}
@media (hover: hover) {
  .header_art6__panel_phone:hover {
    color: var(--color-siniy, #355ddb);
  }
}
@media (max-width: 767px) {
  .header_art6__panel_phone {
    transform: translateY(4px);
  }
}

@media (max-width: 767px) {
  .header_art6__panel_number {
    display: none;
  }
}

.header_art6__panel_icon {
  display: none;
}
@media (max-width: 767px) {
  .header_art6__panel_icon {
    display: inline-block;
    width: 35px;
    height: 35px;
    flex-shrink: 0;
  }
}

.header_art6__panel_callback {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  min-width: 184px;
  height: 40px;
  padding: 8px 16px;
  font-weight: 500;
  font-size: 13px;
  line-height: 185%;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  text-align: center;
  color: var(--color-belyy, #fcfcfc);
  background-color: var(--color-siniy, #355ddb);
  border: 1px solid var(--color-siniy, #355ddb);
  border-radius: 3px;
  transition: color 0.2s ease-out, background-color 0.2s ease-out;
}
@media (hover: hover) {
  .header_art6__panel_callback:hover {
    color: var(--color-siniy, #355ddb);
    background-color: var(--color-belyy, #fcfcfc);
  }
}
@media (max-width: 767px) {
  .header_art6__panel_callback {
    display: none;
  }
}

.header_art6__show_mobile {
  display: none;
}
@media (max-width: 767px) {
  .header_art6__show_mobile {
    flex-shrink: 0;
    display: block;
    width: 40px;
    height: 40px;
    transition: color 0.2s ease-out;
  }
}
@media (max-width: 767px) and (hover: hover) {
  .header_art6__show_mobile:hover {
    color: var(--color-siniy, #355ddb);
  }
  .header_art6__show_mobile:hover .header_art6__burger span, .header_art6__show_mobile:hover .header_art6__burger::before, .header_art6__show_mobile:hover .header_art6__burger::after {
    color: var(--color-siniy, #355ddb);
    background-color: var(--color-siniy, #355ddb);
  }
}

.header_art6__dropdown {
  position: absolute;
  top: calc(100% + 4px);
  left: -20px;
  z-index: 2;
  width: 300px;
  padding: 30px 0;
  background-color: var(--color-white, #fff);
  background-color: bisque;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: opacity 0.2s ease-out, visibility 0.2s ease-out;
}
.item_has_dropdown.active .header_art6__dropdown {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
}

.header_art6__dropdown_item {
  position: relative;
}
.header_art6__dropdown_item:not(:last-child) {
  margin-bottom: 18px;
}

.header_art6__dropdown_link,
.header_art6__inner_link {
  display: block;
  padding: 0 20px;
  font-weight: 500;
  font-size: 16px;
  line-height: 140%;
  color: var(--color-chernyy);
  transition: color 0.2s ease-out;
}
@media (hover: hover) {
  .header_art6__dropdown_link:hover,
.header_art6__inner_link:hover {
    color: var(--color-siniy, #355ddb);
  }
}

.header_art6__inner_list {
  position: absolute;
  top: 0;
  left: calc(100% - 26px);
  display: block;
  width: 300px;
  padding: 30px 20px;
  background-color: var(--color-header-inner-list-bg, #f4f4f4);
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
